"use client";

import React, { useEffect, useState } from "react";

const Testimonial = () => {
  const [review, setReview] = useState([]);

  useEffect(() => {
    fetch("/review.json")
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        return setReview(data);
      })
      .catch((err) => {
        console.log(err.message);
      });
  }, []);


  return (
    <div className="px-4 sm:px-10 lg:px-20 py-5">
        <ul>
            {
                review.map((item) => {
                    return(
                       <p>xfg</p>
                    )
                })
            }
        </ul>
    </div>
  );
};

export default Testimonial;
